import classNames from "classnames";
import pic1 from "./../../images/pic-heart.svg"
import pic2 from "./../../images/pic-human.svg"
import pic3 from "./../../images/pic_hz.svg"


export default function Modal2({show, onClose}) {
    const modalClass = classNames({
        "modal": true,
        'modal_opened': show
    })


    return (
        <>
            <section className={modalClass}>
                <div className="modal__wrapper">
                    <button className="modal__button"  onClick={onClose}>X</button>
                    <p className="modal__text modal__text_accent">     
                        : 
                                 3   77%
                        <sup>1</sup>.
                    </p>
                    <p className="modal__text">  5         
                        40%
                        .<sup>2</sup>         
                        ,
                             .</p>
                    <p className="modal__title">  </p>
                    <ul className="cards3">
                        <li className="cards3__card">
                            <img className="cards3__pic" src={pic1} alt=" "/>
                            <p className="cards3__num">45,5%</p>
                            <p className="cards3__title">  <span className="cards3__title_accent">  </span><sup>1</sup>
                            </p>
                            <p className="cards3__text">   (N=385)</p>
                        </li>
                        <li className="cards3__card">
                            <img className="cards3__pic" src={pic2} alt=" "/>
                            <p className="cards3__num">55,3%</p>
                            <p className="cards3__title">  <span className="cards3__title_accent">  </span><sup>1</sup>
                            </p>
                            <p className="cards3__text">    (N=2887)</p>
                        </li>
                        <li className="cards3__card">
                            <img className="cards3__pic" src={pic3} alt="  "/>
                            <p className="cards3__num">50,5%</p>
                            <p className="cards3__title">  <span className="cards3__title_accent">   </span><sup>1</sup>
                            </p>
                            <p className="cards3__text">    (N=42642)</p>
                        </li>
                    </ul>
                </div>

            </section>
        </>
    );
}