import classNames from 'classnames';
import img from './../../images/diag.svg'


export default function Modal1({show, onClose}) {


    const modalClass = classNames({
        "modal": true,
        'modal_opened': show
    })

    return (
        <>

            <section className={modalClass}>
                <div class="modal__wrapper">
                    <button class="modal__button" onClick={onClose}>X</button>
                    <p class="modal__text">   <span class="modal__text_accent">   (COPDGene)</span>
                           ,   55 ,   <span
                            class="modal__text_accent">  (66%)</span>
                        ,    60-64     .</p>
                    <p class="modal__text">       
                         , 
                              <span
                            class="modal__text_accent">   </span>,   
                        ,
                                  .</p>
                    <img class="modal__img" src={img}
                         alt="      , %"/>
                </div>
            </section>
        </>
    );
}